'use client';

import React from 'react';
export const dynamic = 'force-dynamic';


export default function BookDescription({ params }: { params: { id: string } }) {
  const { id } = params;
  console.log('Params:', params.id);
  return (
    <div>
      <h1>Book Description</h1>
      <p>Book ID: {id}</p>
    </div>
  );
}
